@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
*{margin:0;padding:0;font-family:'Roboto', sans-serif;font-size:1rem;box-sizing:border-box}a{color:#fff}.page{padding:0.8rem}.header{display:flex;justify-content:space-between;align-items:center;margin-bottom:2rem;padding-bottom:0.5rem;border-bottom:1px solid #eee}.header__title{font-size:1.5rem;color:#636363}.header__search{padding:0.5rem;width:20rem;border:2px solid #ddd;border-radius:0.5rem;background-color:#f8f8f8;outline:none;transition:all ease 0.3s}.header__search:focus{border-color:#bddff5;background-color:#fff}.club__list{display:flex;justify-content:space-between;flex-wrap:wrap}.club__item{margin-bottom:1rem;padding:1rem;width:20rem;list-style:none;border-radius:0.5rem;border:1px solid #ddd;background-color:#c9e4f6;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px;font-weight:bold;color:#504b4b}.club__item>*{margin:3px 0}.club__label{display:inline-block;font-weight:normal;width:10rem;color:#504b4b}.club__name{font-weight:bold}.new-club__form{display:flex;flex-direction:column}.new-club__title{padding:3rem 0 1rem 0;font-size:1.5rem;color:#636363}.new-club__option{padding:3rem 0 1rem 0;font-size:1rem;color:#636363}.new-club__input{margin-right:1rem;margin-bottom:1rem;padding:0.5rem;width:25rem;border:2px solid #ddd;border-radius:0.5rem;background-color:#f8f8f8;outline:none;transition:all ease 0.3s}.new-club__input:focus{border-color:#636363;background-color:#fff;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px}.new-club__btn{padding:0.5rem 1.5rem;border-radius:0.5rem;border:none;border:2px solid #636363;background-color:#636363;font-weight:bold;color:#fff;cursor:pointer;transition:all ease 0.3s;width:10rem}.new-club__btn:hover{border-color:#7990a0;background-color:#7990a0}

